* {
    padding: 0px;
    margin : 0px;
    border : 0px;
}

*,
*:before,
*:after {
    -moz-box-sizing   : border-box;
    -webkit-box-sizing: border-box;
    box-sizing        : border-box;
}

aside,
nav,
footer,
header,
section {
    display: block;
}

body {
    line-height             : 1;
    -ms-text-size-adjust    : 100%;
    -moz-text-size-adjust   : 100%;
    -webkit-text-size-adjust: 100%;
}

main {
    flex: 1 1 auto;
}

body,
html {
    height: 100%;
}

input::-ms-clear {
    display: none;
}

button {
    background-color: transparent;
    cursor          : pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border : 0;
}

a,
a:visited {
    text-decoration: none;
    color          : #000;

}

a:active {
    color: rgba(255, 255, 255, 0.5);
}

a:hover {
    text-decoration: underline;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size  : inherit;
}